import "./PhotoForm.css";
import React from 'react';


export default class PhotoForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            'types': {
                'Foto': false,
                'Special': false,
            },
            'specials': {
                'Camera & Kwast': false,
                'Boxbeeld':false,
                'Geënsceneerd': false,
            },

            'themes': {
                'Landschap': false,
                'Stad': false,
                'Buiten': false,
                'Reizen': false,
                'Water': false,
                'Mensen': false,
                'Abstract': false,
                'Industrieel': false,
                'Scenes': false,
            },
            'colors': {
                'Blauw': false,
                'Geel': false,
                'Groen': false,
                'Rood': false,
                'Oranje': false,
                'Paars': false,
                'Kleurrijk': false,
            }
        }
    }

    render() {
        return (
            <form onSubmit={this.onFormSubmit.bind(this)}>
                <p>Maak keuze uit: </p>
                { this.renderTypes() }
                <p>Maak keuze uit</p>
                { this.renderSpecials() }
                <p>Beschrijf het werk:</p>
                <input type='textarea' name='beschrijving' />
                <p>Kies de thema's:</p>
                { this.renderThemes() }
                <p>Kies de kleuren:</p>
                { this.renderColors() }
                <p>Sleep hier de foto naar toe:</p>
                <input type='file'  name='picture' />
                <br />
                <input type="submit" value="Verzenden" />
            </form>
        )
    };

    renderSpecials() {
        const specials = ['Camera & Kwast', 'Boxbeeld', 'Geënsceneerd']
        return specials.map((special, i) => {
            return (
                <label key={i} >
                    {special}
                    <input
                        type='checkbox'
                        name={special}
                        onChange={ this.onSpecialChange.bind(this)}
                        value={ this.state.specials[special]} />
                </label>
            )
        })
    }

    onSpecialChange(e) {
        const val = e.target.checked;
        const name = e.target.name;
        let updatedSpecials = Object.assign({}, this.state.specials, {[name]: val})
        this.setState({
            'specials': updatedSpecials
        })
    }

    renderTypes() {
        const types = ['Foto', 'Special']
        return types.map((type, i) => {
            return (
                <label key={i} >
                    {type}
                    <input
                    type='checkbox'
                    name={type}
                    onChange={this.onTypeChange.bind(this)}
                    value={this.state.types[type]}/>
                </label>
            )
        })
    }

    onTypeChange(e){
        const val = e.target.checked;
        const name = e.target.name;

        let updatedTypes = Object.assign({}, this.state.types, {[name]: val})
        this.setState({
            'types': updatedTypes
    })
    }

    renderThemes(){
        const themes = ['Landschap', 'Stad', 'Buiten', 'Reizen', 'Water', 'Mensen',  'Abstract', 'Industrieel', 'Scenes'];
        return themes.map((theme, i) => {
            return (
                <label key={i}>
                    {theme}
                    <input
                    type="checkbox"
                    name={theme}
                    onChange={this.onThemeChange.bind(this)}
                    value={this.state.themes[theme]}/>
                </label>
            )
        })
    }

    onThemeChange(e) {
        const val = e.target.checked;
        const name = e.target.name;

        let updatedThemes = Object.assign({}, this.state.themes, {[name]: val})
        this.setState({
            'themes': updatedThemes
        })
    }

    renderColors(){
        const colors = ['Blauw', 'Geel', 'Groen', 'Rood', 'Oranje', 'Paars', 'Kleurrijk'];
        return colors.map((color, i) => {
            return (
                <label key={i}>
                    {color}
                    <input
                        type="checkbox"
                        name={color}
                        onChange={this.onColorChange.bind(this)}
                        value={this.state.colors[color]}/>
                </label>
            )
        })
    }

    onColorChange(e) {
        const val = e.target.checked;
        const name = e.target.name;

        let updatedColors = Object.assign({}, this.state.colors, {[name]: val})
        this.setState({
            'colors': updatedColors
        })
    }

    onFormSubmit(e) {
        e.preventDefault();
        console.log('types', this.state.types);
        console.log('specials', this.state.specials);
        console.log('description')
        console.log('themes', this.state.themes);
        console.log('colors', this.state.colors);
        console.log('file')

    }
}
